.audio-player {
  background: white;
  text-align: center;
  display: flex;
  flex-flow: row;
  margin: 4rem 0 4rem 0; }
  .audio-player .album-image {
    min-height: 100px;
    width: 110px;
    background-size: cover; }
  .audio-player .player-controls {
    align-items: center;
    justify-content: center;
    margin-top: 2.5rem;
    flex: 3; }
    .audio-player .player-controls .scrubber {
      width: 100%;
      height: 5px;
      position: relative;
      border-left: 0px;
      border-bottom: 0px;
      overflow: hidden; }
    .audio-player .player-controls .progress {
      position: absolute;
      top: 0px;
      left: 0px;
      height: 5px;
      width: 0px;
      background-color: blue;
      z-index: 1;
      transition: width .05s; }
    .audio-player .player-controls .loaded {
      position: absolute;
      top: 0px;
      left: 0px;
      height: 5px;
      width: 0px;
      background-color: rgba(0, 0, 255, 0.2); }
    .audio-player .player-controls progress {
      width: 90%; }
    .audio-player .player-controls progress[value] {
      -webkit-appearance: none;
      appearance: none;
      background-color: white;
      color: blue;
      height: 5px; }
    .audio-player .player-controls progress[value]::-webkit-progress-bar {
      background-color: white;
      border-radius: 2px;
      border: 1px solid #dfdfdf;
      color: blue; }
    .audio-player .player-controls progress::-webkit-progress-value {
      background-color: blue; }
    .audio-player .player-controls h3 {
      font-size: 1em;
      margin-bottom: 0; }
    .audio-player .player-controls small {
      font-size: .6em; }
    .audio-player .player-controls .start-time {
      display: none; }
  .audio-player .play-pause .play {
    background-image: url("http://www.lukeduncan.me/images/play-button.png");
    background-size: cover;
    width: 75px;
    height: 75px;
    margin: 2rem 0 2rem 2rem; }
  .audio-player .play-pause .pause {
    background-image: url("http://www.lukeduncan.me/images/pause-button.png"); }

